exports.push([module.id, "\n.el-tabs--border-card[data-v-0af6389b] {\n  box-shadow: none;\n}\n", "",{"version":3,"sources":["src/components/TabPage.vue"],"names":[],"mappings":";AA0EA;EACA,gBAAA;AACA","file":"TabPage.vue?vue&type=style&index=0&id=0af6389b&scoped=true&lang=css&","sourcesContent":["<template>\r\n  <div>\r\n    <el-tabs\r\n      type=\"border-card\"\r\n      v-model=\"activeName\"\r\n      @tab-click=\"handleClick\"\r\n      @tab-remove=\"handleRemove\">\r\n      <el-tab-pane\r\n        v-for=\"item in pages\"\r\n        :key=\"item.label\"\r\n        :label=\"item.label\"\r\n        :name=\"item.name\"\r\n        :closable=\"item.closable\">\r\n        <slot :name=\"item.slotName\"></slot>\r\n      </el-tab-pane>\r\n    </el-tabs>\r\n  </div>\r\n</template>\r\n\r\n<script>\r\n  export default{\r\n    prop:[],\r\n    data(){\r\n      return{\r\n        activeName: 'default',\r\n        pages: [],\r\n      }\r\n    },\r\n    mounted() {\r\n      this.$on('open-tab', function(tabPage){\r\n        this.openTabPage(tabPage);\r\n      });\r\n      this.$on('remove-tab', function(tabPage){\r\n        this.handleRemove(tabPage);\r\n      });\r\n    },\r\n    methods: {\r\n      handleRemove(tab) {\r\n        var pageIndex = -1;\r\n        this.pages.forEach((item, index) => {\r\n          if((item.name == tab.name || tab)){\r\n            pageIndex = index;\r\n          }\r\n        });\r\n        if(pageIndex >= 0){\r\n          this.activeName = this.pages[pageIndex-1].name;\r\n          this.pages.splice(pageIndex, 1);\r\n        }\r\n      },\r\n      handleClick(tab, event) {\r\n        console.log(tab, event);\r\n        console.log(tab.name, tab.label);\r\n      },\r\n      openTabPage(tabPage,noOpen){\r\n        if(this.pages.some(item => {\r\n            if(tabPage.name == item.name){\r\n              item.label = tabPage.label;\r\n              return true;\r\n            }\r\n          })){\r\n          this.activeName = tabPage.name;\r\n        }\r\n        else{\r\n          this.pages.push(tabPage);\r\n          if(!noOpen){\r\n            this.activeName = tabPage.name;\r\n          }\r\n        }\r\n      }\r\n    }\r\n  }\r\n</script>\r\n\r\n<style scoped>\r\n  .el-tabs--border-card {\r\n    box-shadow: none;\r\n  }\r\n</style>\r\n"]}]);
